import React from 'react';
import './ucapan.css';

function Ucapan(){
    return(
        <div className="Ucapan">
            <h4>Sidangtulation</h4>
            <h5>Halo <span className="fitri">Fitri...</span></h5>
            <p>It's been a long time since I wrote a <span className="letterr">letter</span> like this. First of all <span className="congrats">Congrats ya...</span> Honestly, I'm shocked (in a good way) knowing that you're gonna go for the <span className="sidang">Sidang</span> before me. Waktu tau kabar itu dari Puspita, buru-buru dah langsung tak kelarin skripsiku, anda memang salah satu partner yang membangkitkan semangat wkwkw.</p>
            <p>Second, I hope you like the gift. Honestly aku bingung mau kasih <span className="special">something special</span> apa, dan karena bingung akhirnya aku putusin buat bikin <span className="virtual">virtual gift card</span> aja hehe.</p>
            <p>Third, dengan sidangnya kamu, aku berharap semua yang udah kamu peroleh selama kuliah bisa bermanfaat bagi kamu kedepannya, ilmu kamu berkah dan tentunya dapat membantu kesuksesanmu kedepan.</p>
            <p>Terakhir, mungkin aku minta maaf kalau <span className="seandainya">seandainya</span> nanti ga bisa dateng di wisuda mu, tapi aku harap hubungan ini bisa berjalan terus walau udah sama sama lulus kuliah.</p>
            <p>Tambahan, aku sekali lagi minta maaf buat semua salah yang kulakuin selama kuliah ini terutama soal video <span className="eigen">eigen</span> dan mungkin kata-kata yang bilang "gausah cerita-cerita ke aku". Jujur kalau inget itu sekarang, pengen balik lagi dengerin kamu cerita apapun kayak dulu atau bahkan berantem kayak dulu terus tiba-tiba u hapus DP dan bikin story WA wkwkw.</p>
            <p className="last">Sekali lagi, congrats ya atas sidangnya. Sukses selalu, dan salam juga buat orang tua terutama ibumu di rumah...</p>
        </div>
    )
}

export default Ucapan;